.LoadingSpinner {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.LoadingSpinner_msg {
  margin-top: 20px;
  text-align: center;
  font-style: italic;
}

.LoadingSpinner_spinner {
  border-radius: 100%;
  border: 5px solid rgba(0, 0, 0, 0.1);
  border-right-color: black;
  width: 50px;
  height: 50px;
  -webkit-animation: LoadingSpinner_spin 1.2s infinite linear;
          animation: LoadingSpinner_spin 1.2s infinite linear;
}

@-webkit-keyframes LoadingSpinner_spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes LoadingSpinner_spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



