.OpenWithOverleaf {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  flex-align: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.OpenWithOverleaf_button {
  margin: 0 auto;
  padding: 15px;
  font-size: 18px;
  background-color: green;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  border: none;
}

.OpenWithOverleaf_done {
  font-size: 30px;
  text-align: center;
  margin-bottom: 10px;
}

.OpenWithOverleaf_reset {
  border: none;
  text-decoration: underline;
  background: none;
  cursor: pointer;
  padding: 3px;
  margin-top: 10px;
}



